.lame_minslider {
  padding: 20px 0 20px;

  &.container {
    max-width: 1128px;
  }
  
  h2 {
    font-weight: 500;
    font-size: 38px;
    text-align: center;
    text-transform: uppercase;

    @media #{$max-sm} {
      font-size: 30px;
    }
  }

  .slick-list {
    margin-left: 0px;
    width: 100%;
  }

  .wrapper-slider-category {
    position: relative;

    .nav-you-are {
      cursor: pointer;
      height: 100%;
      position: absolute;
      top: 0;
      transition: 0.125s opacity ease-in-out;
      width: 60px;
      display: flex !important;
      justify-content: center;
      flex-direction: column;
      align-items: center;

      @media #{$xs} {
        width: 120px;
      }

      @media #{$md} {
        width: 160px;
      }
    }

    .prev {
      left: -118px;
      background: url('../img/mslider/arrow_gleft.svg') no-repeat center;

      @media #{$max-sm} {
        left: -20px;
        background-color: $white;
        height: 60px;
        top: 248px;
      }

      &:hover {
        background: url('../img/mslider/arrow_gleft.svg') no-repeat center;

        @media #{$max-sm} {
          background: $brown url('../img/mslider/arrow_gleft.svg') no-repeat center;
        }
      }

      svg {
        @include transform(rotate(90deg));
        margin-left: 15px;
      }
    }

    .next {
      right: -118px;
      background: url('../img/mslider/arrow_gright.svg') no-repeat center;

      @media #{$max-sm} {
        right: -14px;
        background-color: $white;
        height: 60px;
        top: 248px;
      }

      svg {
        @include transform(rotate(-90deg));
        margin-right: 15px;
      }

      &:hover {
        background: url('../img/mslider/arrow_gright.svg') no-repeat center;

        @media #{$max-sm} {
          background: $brown url('../img/mslider/arrow_gright.svg') no-repeat center;
        }
      }
    }
  }

  .slick-track {
    margin-left: -10px;
    width: calc(100% + 20px);
  }

  .slick-slide {
    width: 265px !important;
    padding: 20px 0;
    margin-left: 10px;
    margin-right: 10px;

    .slider-category-entry {
      > div {
        &:first-of-type {
          height: 165px;
        }
      }
      
      h3 {
        font-size: 26px;
        text-align: center;
        font-weight: 400;
        line-height: 1.4em;
      }

      div {
        text-align: center;
        font-size: 18px;
        line-height: 1.6em;
      }
    }
  }

  .mobile-wrapper {
    text-align: center;

    .slider-category-entry {
      margin-bottom: 1.6em;
      padding: 1em;

      > div {
        &:first-of-type {
          min-height: 160px;
        }
      }
    }
  }

  .slider-deactivated, .three-wrapper {
    display: flex;
    justify-content: center;

    .slider-category-entry {
      flex-wrap: nowrap;
      width: 265px !important;
      margin: 10px;

      > div {
        &:first-of-type {
          height: 165px;
        }
      }

      h3 {
        font-size: 26px;
        text-align: center;
        font-weight: 400;
        line-height: 1.4em;
      }

      div {
        text-align: center;
        font-size: 18px;
        line-height: 1.6em;
      }
    }
  }

  .slider-deactivated {
    flex-flow: row wrap;
    justify-content: center;

    .slider-category-entry {
      width: 255px !important;
      margin: 10px;
      margin-bottom: 30px;
    }
  }
}
