$path: '../fonts/';

@font-face {
  font-family: 'agrafie';
  src: url($path + 'AgrafieLLMedium_0.eot'),
  url($path + 'AgrafieLLMedium_0.eot?#iefix') format('embedded-opentype'),
  url($path + 'AgrafieLLMedium_0.woff') format('woff'),
  url($path + 'AgrafieLLMedium_0.ttf') format('truetype'),
  url($path + 'AgrafieLLMedium_0.svg#brandonbold') format('svg');
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Brandon Grotesque';
  src: url($path + 'BrandonGrotesque-Bold.woff2') format('woff2'),
  url($path + 'BrandonGrotesque-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}

