.logo-huttopiac {
    display: inline-block;
    overflow: hidden;
    width: 232px;
    margin: 5px 0;
    height: 50px;

    @media #{$max-sm} {
        width: 220px;
        padding-top: 10px;
    }

    a {
        display: inline-block;
        line-height: 1;
    }

    img {
        height: 100%;
        width: 232px;

        @media #{$max-sm} {
            width: 100%;
        }
    }
}

